<script>
import $ from "jquery";
import axios from "axios";
export default{
    name: 'AdminView',
  data () {
    return {
      value: true,
      userdata: [],
      userdatas: [],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
    }
  },
  created() {
    this.getuserdata();
    this.isAuthenticated();
    this.buser_data();
    this.userdata = this.buser_data();
    this.user_role();
    if(this.isAuthenticated() == true && this.user_role() == 4){
      console.log('its ok');
      // this.$router.push({name: 'usersidebar'});
    }else {
      this.$router.push({name: 'adminlogin'});
      console.log('not ok');

    }
  },
  computed: {
    lastWord() {
      const words = this.userdatas.name.split(' ');
      return words[words.length - 1];
    },
    firstCharOfLastWord() {
      return this.lastWord.charAt(0);
    }
  },
  methods: {
    getuserdata(){
      axios.get('userdata')
          .then((resp) =>{
            this.userdatas = resp.data.user;
          })
    },
    adminLogout() {
      this.$store.dispatch('logout');
      this.$router.push('/admin/login');
    },
    isAuthenticated() {
      return this.$store.state.isAuthenticated;
    },
    user_role() {
      return this.$store.state.userData.user_role;
    },
    buser_data() {
      return this.$store.state.userData;
    },
    toggleValue() {
      this.value = !this.value; // Toggle the value between true and false
    },
  },
  watch: {
    value(newValue) {
      console.log('Value changed:', newValue);
    },
  },
}
$(document).ready(function(){
  $(".v-app-bar__nav-icon").click(function(){
    $("main").toggleClass("love");
  });
});
</script>
<style scoped>
.love{
  padding: 5px 0 0 260px !important;
  }
@media (min-width: 577px) {
  .aright {
    max-width: calc(100vw - 240px - calc(var(--bs-gutter-x) * 0.5));
    width: 100%;
    left: 240px;
    position: absolute;
  }
  .aleft{
    max-width: 240px;
    min-width: 240px;
  }
}
@media (max-width: 576px) {
  .love {
    padding: 0px !important;
    }
  .bdash > div{
    height: calc(100vH - -50px);
    top: 121px;
    width: 60px;
    overflow: auto;
    transform: translateX(0%);
    position: fixed;
  }
  .rright{
    max-width: calc(100vw - 60px);
    width: 100%;
  }
  .rleft{
    max-width: 60px;
    min-width: 60px;
  }
}
</style>